@use "global" as *;

.p-about-dropdown-label {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  background-color: $red;
  color: $white;
  font-weight: $medium;
  line-height: 1;
  letter-spacing: 0.32px;
  transition: all 0.3s;
  font-size: rem(12);
  padding: rem(10.4) rem(28);
  @include mq("md") {
    padding: min(vw($full, 6), rem(6)) min(vw($full, 42), rem(42)) min(vw($full, 6), rem(6)) min(vw($full, 14), rem(14));
    gap: 20px;
    font-size: min(vw($full, 16), rem(16));
  }
}

.p-about-dropdown-label::before {
  content: "";
  position: absolute;
  background-image: url(".././images/common/dropdown-arrow-white.png");
  background-size: cover;
  background-repeat: no-repeat;
  transition: transform 0.3s;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  right: rem(9);
  width: 0.4em;
  height: 0.25em;
  @include mq("md") {
    right: rem(12);
    width: 0.625em;
    height: 0.4375em;
  }
}
