@use "global" as *;

.p-assortment-item {
  display: flex;
  justify-content: center;
}

.p-assortment-item button {
  padding: 0;
}

.p-assortment-item__img {
  background: $white;
  aspect-ratio: 1/1;
  box-shadow: -7px 8px 8px 0px rgba(195, 146, 94, 0.04);
  z-index: 1;
  border-radius: 30px;
  width: rem(352);
  height: rem(352);
  margin-inline: auto;
  padding: rem(50) rem(10);
  @include mq("md") {
    aspect-ratio: 188/184;
    padding: min(vw($full, 53), rem(53)) min(vw($full, 13), rem(13));
    border-radius: 74px;
  }
}

.p-assortment-item__img img {
  height: auto;
  object-position: top left;
  object-fit: contain;
  margin-inline: auto;
  width: rem(304);
  aspect-ratio: 304/294;
  @include mq("md") {
    width: rem(314);
    aspect-ratio: 314/233;
  }
}

.p-assortment-item__title {
  font-weight: $bold;
  line-height: 1.6;
  letter-spacing: 0.36px;
  text-align: center;
  margin-top: rem(15);
  color: $black;
  @include mq("md") {
    font-size: rem(18);
    margin-top: rem(15);
    text-align-last: left;
  }
}

.p-assortment-item__cats {
  display: flex;
  gap: rem(5);
  justify-content: center;
  color: $black;
  margin-top: rem(5);
  @include mq("md") {
    gap: min(vw($full, 18), rem(18));
    justify-content: start;
  }
}

.p-assortment-item__cat {
  font-weight: $medium;
  line-height: 1.8;
  letter-spacing: 0.32px;
  @include mq("md") {
    font-size: min(vw($full, 16), rem(16));
  }
}
