@use "global" as *;

.p-fruits {
  padding-bottom: rem(130);
  padding-top: vw(375, 90);
  z-index: 100;
  background-color: $beige;
  @include mq("md") {
    padding-bottom: rem(205);
    padding-top: rem(222);
  }
}

.p-fruits::after {
  position: absolute;
  content: "";
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  left: 0;
  width: 100%;
  height: vw(375, 57);
  top: rem(-15);
  background-image: url(".././images/top/about-bg-bottom-red__sp.png");
  @include mq("md") {
    top: rem(-29);
  }
}

.p-fruits::before {
  position: absolute;
  content: "";
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(".././images/top/fruits-bg__sp.png");
  background-size: cover;
  background-position: bottom -19px left;
  background-repeat: no-repeat;
  left: 0;
  top: rem(-14);
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: $beige;
  @include mq("md") {
    background-image: url(".././images/top/fruits-bg__pc.png");
    top: rem(-27);
    background-position: bottom -66px left;
  }
}

.p-fruits__title__box {
  display: inline-block;
}

.p-fruits__text {
  font-weight: $medium;
  line-height: 2;
  letter-spacing: 0.32px;
  margin-top: rem(14);
  @include mq("md") {
    line-height: 2.2;
    margin-top: rem(14.4);
  }
}

.p-fruits__cards {
  margin-top: rem(50);
  display: grid;
  gap: rem(40);
  @include mq("md") {
    gap: rem(60);
    margin-top: rem(70);
  }
}

.p-fruits__btn {
  display: flex;
  justify-content: center;
  margin-top: rem(40);
  @include mq("md") {
    margin-top: rem(80);
  }
}
