@use "global" as *;

.p-fruits-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  border-radius: 28px;
  background: $white;
  box-shadow: -7px 8px 8px 0px rgba(195, 146, 94, 0.04);
  padding: rem(86) rem(20) rem(53);
  transition: filter 0.3s ease;
  @include mq("md") {
    border-radius: 74px;
    padding: rem(80) rem(80) rem(80) rem(90);
    flex-direction: row;
    gap: rem(101);
  }
}

.p-fruits-card::before,
.p-fruits-card::after {
  content: "";
  display: block;
  position: absolute;
}

.p-fruits-card::before {
  background-image: url(".././images/common/tag.png");
  background-size: contain;
  background-position: center;
  width: rem(42);
  height: rem(53);
  aspect-ratio: 42/53;
  top: rem(-18);
  right: 0;
  @include mq("md") {
    width: rem(83);
    height: rem(103);
    aspect-ratio: 83/103;
    top: rem(-36);
    right: rem(-14);
  }
}

.p-fruits-card::after {
  background-image: url(".././images/top/fruits-namber01.png");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  width: rem(121);
  height: rem(42);
  aspect-ratio: 121/42;
  top: rem(28);
  left: rem(20);
  @include mq("md") {
    width: rem(156);
    height: rem(56);
    aspect-ratio: 156/56;
    top: rem(84);
    left: rem(90);
  }
}

.p-fruits-card:first-child::after {
  background-image: url(".././images/top/fruits-namber01.png");
}

.p-fruits-card:nth-child(2):after {
  background-image: url(".././images/top/fruits-namber02.png");
}

.p-fruits-card:nth-child(3):after {
  background-image: url(".././images/top/fruits-namber03.png");
}

.p-fruits-card__text-block {
  display: contents;
  @include mq("md") {
    display: block;
  }
}

.p-fruits-card__title {
  color: $red;
  font-family: $second-font-family;
  font-size: rem(48);
  line-height: 1;
  letter-spacing: 0.96px;
  white-space: nowrap;
  @include mq("md") {
    font-size: rem(62);
    letter-spacing: 1.24px;
    margin-top: rem(100);
  }
}

.p-fruits-card__img {
  width: 100%;
  max-width: rem(315);
  aspect-ratio: 315/196;
  margin-inline: auto;
  margin-top: rem(26);
  @include mq("md") {
    max-width: min(vw($full, 531), rem(531));
    height: min(vw($full, 370), rem(370));
    aspect-ratio: 531/370;
    margin-top: 0;
  }
}

.p-fruits-card__img,
.p-fruits-card__img img {
  height: 100%;
  border-radius: 24px;
}

.p-fruits-card__text {
  text-align: center;
  font-weight: $medium;
  line-height: 1.8;
  letter-spacing: 0.32px;
  margin-top: rem(20);
  order: 1;
  @include mq("md") {
    font-size: max(vw($full, 16), 14px);
    font-weight: $medium;
    line-height: 1.8;
    letter-spacing: 0.32px;
    margin-top: rem(50);
    text-align-last: left;
  }
}

.p-fruits-card__btn {
  margin-top: rem(20);
  order: 2;
  @include mq("md") {
    text-align-last: left;
    margin-top: rem(50);
  }
}

.p-fruits-card__btn button {
  display: inline-block;
  color: $red;
  font-weight: $medium;
  line-height: 2.2;
  letter-spacing: 0.32px;
  border-bottom: 1px solid $red;
  background-image: url(./../images/common/arrow-white.png);
  background-size: 1.5em 1.5em;
  background-repeat: no-repeat;
  background-position: right;
  padding: 0 rem(46) 0 0;
  margin-inline: auto;
}

.p-fruits-card:hover {
  filter: brightness(0.8);
  .p-fruits-card__btn a {
    background-size: 2.1em 2.1em;
  }
}
